<template>
    <div>

        <v-list v-for="data in datas" :key="data.uid">
            <v-list-item :to="data.uid">{{ data.name }}:{{ data.detail }}
                <a v-bind:href="`http://localhost:8080/read/${data.uuid}`">
                    http://localhost:8080/read/{{data.uuid}}
                </a>
            </v-list-item>
        </v-list>
    </div>
</template>

<script>
    import firebase from '../firebase';
    export default {
        name: "Nodes",
        data(){
            return{
                datas:[]
            }
        },
        mounted:function(){
            const nodesQ=firebase.firestore().collection("nodes");
            nodesQ.where("owner", '==',firebase.auth().currentUser.uid)
                .get().then(datas => {
                    datas.forEach(data => {
                        this.datas.push(data.data())
                    });
                })
                .catch(err => {
                    console.log('Error getting documents', err);
                });
         }
    }
</script>

