<template>
    <div>


        <p>who:{{who}}</p>
        <p>name:{{data.name}}</p>
        <p>detail:{{data.detail}}</p>
        <v-container class="grey lighten-5">
            <v-row class='mb-6' no-gutters>
                <v-col>
                    <p>受け取り場所</p>
                    <GmapMap
                            :center="{lat:data.pre_lat, lng:data.pre_lng}"
                            :zoom="13"
                            map-type-id="roadmap"
                            style="width: 350px; height: 300px"
                    >
                        <GmapMarker
                                :position="{lat: data.pre_lat, lng: data.pre_lng}"
                        />
                    </GmapMap>
                    <v-card class="pa-2" outlined tile>
                        <p>前へ</p>
                        <v-list v-for="node in data.pre_nodes" :key="node">
                            <a v-bind:href="`http://localhost:8080/read/${node}`">
                                http://localhost:8080/read/{{node}}
                            </a>
                        </v-list>
                    </v-card>
                </v-col>
                <v-col>
                    <p>受け渡し場所</p>
                    <GmapMap
                            :center="{lat:data.post_lat, lng:data.post_lng}"
                            :zoom="13"
                            map-type-id="roadmap"
                            style="width: 350px; height: 300px"
                    >
                        <GmapMarker
                                :position="{lat: data.post_lat, lng: data.post_lng}"
                        />
                    </GmapMap>
                    <v-card class="pa-2" outlined tile>
                        <p>次へ</p>

                        <v-list v-for="node in data.post_nodes" :key="node">
                            <a v-bind:href="`http://localhost:8080/read/${node}`">
                                http://localhost:8080/read/{{node}}
                            </a>
                        </v-list>
                    </v-card>
                </v-col>
            </v-row>
        </v-container>
    </div>
</template>

<script>
    import firebase from "@/firebase";

    export default {
        name: "NodeRead",
        data(){
            return{
                who:null,
                data:null,
            }
        },
        mounted:function(){
            firebase.firestore().collection("nodes").doc(this.$route.params.node_id)
                .get().then(data => {
                this.data=data.data();
                firebase.firestore().collection("users").doc(data.data().owner).get().then(user=>{
                    this.who=user.data().email
                });
                })
                .catch(err => {
                    console.log('Error getting documents', err);
                });
            console.log(this.data);
        }
    }
</script>

