<template>
  <div>
    <div>
      <v-card
              class="mx-auto"
              max-width="344"
      >
        <label>商品情報</label>
        <v-card-text>
          商品名
          <v-text-field
                  label="商品名"
                  single-line
                  v-model="inputProductName">
          </v-text-field>
          詳細
          <v-text-field
                  label="商品詳細"
                  single-line
                  v-model="inputProductDetail">
          </v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-btn
                  color="primary"
                  class="ma-auto" @click="generate">生成
          </v-btn>
        </v-card-actions>
      </v-card>
    </div>
    <v-container style="height: 80px;">
      <v-row
              align-content="center"
              justify="center"
      >
        <v-col cols="6">
          <v-progress-circular
                  v-show="progress"
                  color="deep-purple accent-4"
                  indeterminate
                  rounded
                  height="6"
          ></v-progress-circular>
        </v-col>
      </v-row>
    </v-container>
    <vue-qrcode v-if="targetText" :value="targetText" :options="option" tag="img"></vue-qrcode>

    <div>
      <v-btn
              v-show="moving"
              color="primary"
              class="ma-auto" @click="move">Moving
      </v-btn>
    </div>
  </div>
</template>

<script>
  import VueQrcode from "@chenfengyuan/vue-qrcode";
  import firebase from "../firebase"
  import uuidv4 from 'uuid/v4'
  export default {
    name: 'NodeRegister',
    components: {
      VueQrcode
    },
    data() {
      return {
        progress:false,
        moving:false,
        uuid:null,
        inputProductName: "",
        inputProductDetail:"",
        targetText: "",
        post_lng:null,
        post_lat:null,
        option: {
          errorCorrectionLevel: "M",
          maskPattern: 0,
          margin: 10,
          scale: 2,
          width: 300,
          color: {
            dark: "#000000FF",
            light: "#FFFFFFFF"
          }
        }
      }
    },
    methods: {
      generate () {
        const uuid=uuidv4();
        firebase.firestore().collection('nodes').doc(uuid).set({
          uuid:uuid,
          owner:firebase.auth().currentUser.uid,
          name:this.inputProductName,
          detail:this.inputProductDetail,
          access:firebase.firestore.FieldValue.arrayUnion(firebase.auth().currentUser.uid),
          original_access:firebase.firestore.FieldValue.arrayUnion(firebase.auth().currentUser.uid),
          post_lat:null,
          post_lng:null,
        });
        this.uuid=uuid;
        this.targetText = "http://localhost:8080/"+uuid;
        this.moving=true;
      },
      async move(){
        const uuid= this.uuid;
        const next_uuid=uuidv4();
        navigator.geolocation.getCurrentPosition(function(pos){
          firebase.firestore().collection('nodes').doc(uuid).update({
            post_nodes: firebase.firestore.FieldValue.arrayUnion(next_uuid),
            post_lat:pos.coords.latitude,
            post_lng:pos.coords.longitude,
            open:true,
          });
          },function(){});
        this.targetText = "http://localhost:8080/write/"+uuid+"/"+next_uuid;
        console.log(this.targetText);
        this.progress=true;
        await wait(20,next_uuid);
        this.targetText = "http://localhost:8080/read/"+uuid;
        this.progress=false;
        firebase.firestore().collection('nodes').doc(next_uuid).get()
                .then(doc => {
                  if (!doc.exists) {
                    console.log('ない');
                    firebase.firestore().collection('nodes').doc(uuid).update({
                      post_nodes: firebase.firestore.FieldValue.arrayRemove(next_uuid),
                      open:false,
                    });
                  } else {
                    firebase.firestore().collection('nodes').doc(next_uuid).get().then(data=>{
                      const owner = data.data().owner;
                      firebase.firestore().collection('nodes').doc(uuid).update({
                        access:firebase.firestore.FieldValue.arrayUnion(owner),
                      });
                    })
                    firebase.firestore().collection('nodes').doc(uuid).update({
                      open:false,
                    });
                  }
                })
                .catch(() => {
                  firebase.firestore().collection('nodes').doc(uuid).update({
                    post_nodes: firebase.firestore.FieldValue.arrayRemove(next_uuid),
                    open:false,
                  });
                });
      }
    }
  }
  // setTimeout を Promise でラップ
  const wait = (ms,next_uuid)=> new Promise((resolve) => {
    const id =setInterval(() => {
      ms -=1
      if(ms <=0){
        resolve();
        clearTimeout(id);
      }
      firebase.firestore().collection('nodes').doc(next_uuid).get().then(
              (data)=>{
                if(data.exists){
                  resolve();
                  clearTimeout(id);
                }
              })
    },1000)});
</script>

