<template>
  <div>
    <div>
      <v-card
              class="mx-auto"
              max-width="344"
      >
        <label>移動情報</label>
        <v-card-text>
          詳細
          <v-text-field
                  label="詳細"
                  single-line
                  v-model="inputDetail">
          </v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-btn
                  color="primary"
                  class="ma-auto" @click="record">記録
          </v-btn>
        </v-card-actions>
      </v-card>
    </div>
    <v-container style="height: 80px;">
      <v-row
              align-content="center"
              justify="center"
      >
        <v-col cols="6">
          <v-progress-circular
                  v-show="progress"
                  color="deep-purple accent-4"
                  indeterminate
                  rounded
                  height="6"
          ></v-progress-circular>
        </v-col>
      </v-row>
    </v-container>
    <vue-qrcode v-if="targetText" :value="targetText" :options="option" tag="img"></vue-qrcode>
    <div>
      <v-btn
              v-show="moving"
              color="primary"
              class="ma-auto" @click="move">Moving
      </v-btn>
    </div>
  </div>
</template>

<script>
  import VueQrcode from "@chenfengyuan/vue-qrcode";
  import firebase from "../firebase"
  import uuidv4 from 'uuid/v4'
  export default {
    name: 'NodeWrite',
    components: {
      VueQrcode
    },
    data() {
      return {
        pre_lat:null,
        pre_lng:null,
        post_lat:null,
        post_lng:null,
        progress:false,
        moving:false,
        pre_uuid:this.$route.params.pre_node_id,
        uuid: this.$route.params.node_id,
        inputDetail:"",
        targetText: "",
        option: {
          errorCorrectionLevel: "M",
          maskPattern: 0,
          margin: 10,
          scale: 2,
          width: 300,
          color: {
            dark: "#000000FF",
            light: "#FFFFFFFF"
          }
        }
      }
    },
    mounted:function(){
      const uuid=this.uuid;
      const pre_uuid=this.pre_uuid;
      navigator.geolocation.getCurrentPosition(function(pos){
        firebase.firestore().collection('nodes').doc(uuid).set({
          uuid:uuid,
          pre_nodes: firebase.firestore.FieldValue.arrayUnion(pre_uuid),
          owner:firebase.auth().currentUser.uid,
          access:firebase.firestore.FieldValue.arrayUnion(firebase.auth().currentUser.uid),
          original_access:firebase.firestore.FieldValue.arrayUnion(firebase.auth().currentUser.uid),
          pre_lat:pos.coords.latitude,
          pre_lng:pos.coords.longitude,
          post_lat:null,
          post_lng:null,
          open:false,
        });
      },function(){
        console.log('bad');
      });
      firebase.firestore().collection('nodes').doc(pre_uuid).get().then(data=>{
        data.data().original_access.forEach(item=>{
          console.log('access'+item)
          firebase.firestore().collection('nodes').doc(uuid).update({
            access:firebase.firestore.FieldValue.arrayUnion(item),
            original_access:firebase.firestore.FieldValue.arrayUnion(item),
          })
        })
      })
    },
    methods: {
      record(){
        const inputDetail=this.inputDetail;
        firebase.firestore().collection('nodes').doc(this.uuid).update({
          detail:inputDetail
          });
        this.targetText = "http://localhost:8080/read/"+this.uuid;
        this.moving=true;
      },
      async move(){
        const uuid= this.uuid;
        const next_uuid=uuidv4();

        navigator.geolocation.getCurrentPosition(function(pos){
          firebase.firestore().collection('nodes').doc(uuid).update({
            uuid:uuid,
            post_nodes: firebase.firestore.FieldValue.arrayUnion(next_uuid),
            post_lat:pos.coords.latitude,
            post_lng:pos.coords.longitude,
            open:true,
          });
        },function(){});
        this.targetText = "http://localhost:8080/write/"+uuid+"/"+next_uuid;
        console.log(this.targetText);
        this.progress=true;
        await wait(20000);
        this.targetText = "http://localhost:8080/read/"+uuid;
        this.progress=false;
        firebase.firestore().collection('nodes').doc(next_uuid).get()
                .then(doc => {
                  if (!doc.exists) {
                    firebase.firestore().collection('nodes').doc(uuid).update({
                      post_nodes: firebase.firestore.FieldValue.arrayRemove(next_uuid),
                      open:false,
                    });
                  } else {
                    firebase.firestore().collection('nodes').doc(next_uuid).get().then(data=>{
                      firebase.firestore().collection('nodes').doc(uuid).update({
                        access:firebase.firestore.FieldValue.arrayUnion(data.data().owner),
                      });
                    })
                    firebase.firestore().collection('nodes').doc(uuid).update({
                      open:false,
                    });
                  }
                })
                .catch(()=> {
                  firebase.firestore().collection('nodes').doc(uuid).update({
                    post_nodes: firebase.firestore.FieldValue.arrayRemove(next_uuid),
                    open:false,
                  });
                });
      }
    }
  }
  // setTimeout を Promise でラップ
  const wait = ms => new Promise(resolve => setTimeout(() => resolve(), ms));
</script>

